import * as pdf from 'pdfjs-dist/build/pdf';

const url ="https://drive.google.com/file/d/14ZhHOchcBX4l0ijk2fYV4brUoRdU3a3N/view?usp=sharing"

// Loaded via <script> tag, create shortcut to access PDF.js exports.


// The workerSrc property shall be specified.
pdf.GlobalWorkerOptions.workerSrc = '//cdnjs.cloudflare.com/ajax/libs/pdf.js/3.6.172/pdf.worker.js'


// Asynchronous download of PDF
const loadingTask = pdf.getDocument(url);
loadingTask.promise.then(function (pdf)
{
    console.log('PDF loaded');

    // Fetch the first page
    const pageNumber = 1;
    pdf.getPage(pageNumber).then(function (page)
    {
        console.log('Page loaded');

        const scale = 1.5;
        const viewport = page.getViewport({scale: scale});

        // Prepare canvas using PDF page dimensions
        const canvas = document.getElementById("pdf");
        const context = canvas.getContext('2d');
        canvas.height = viewport.height;
        canvas.width = viewport.width;

        // Render PDF page into canvas context
        const renderContext = {
            canvasContext: context,
            viewport: viewport
        };
        const renderTask = page.render(renderContext);
        renderTask.promise.then(function ()
        {
            console.log('Page rendered');
        });
    });
}, function (reason)
{
    // PDF loading error
    
});